<template>
    <div class="home-poles-list">
        <h2 class="text-4xl py-6 md:text-6xl lg:text-4xl">- {{ title }} -</h2>

        <div class="pl-10 py-10 flex flex-col justify-center gap-4 sm:flex-row xs:pl-0">
          <div class="flex flex-row justify-center gap-6 sm:flex-col">
            <Splide class="prueba-slide" :options="slide_options">
              <SplideSlide v-for="(item, index) in poles" :key="index">
                <div class="flex flex-col items-start gap-6 p-3">
                  <figure
                    class="flex relative w-70x100 sm:m-auto sm:w-100x20 md:w-100x50 lg:w-100x70 xl:w-100x90"
                  >
                    <img
                      :style="{filter: 'drop-shadow(11px 14px 7px #000)', width: '100%'}"
                      :src="item.img"
                      alt="polo1"
                    />
                    <figcaption
                      class="absolute top-0 left-0 w-full leading-10 xs:leading-none sm:rounded sm:text-6xl lg:text-10xl"
                    >{{ item.name }}</figcaption>
                  </figure>
                  <button
                    class="btn-destinos uppercase font-DIN-Bold shadow-h-blue-md bg-orange text-2xl text-white rounded-75 px-8 py-1 md:px-16 md:mx-auto mx-4"
                    @click="goSinglePole(item.keyword, {
                      country: item.name + ' - ' + item.country,
                      pole: item.name,                      
                    })"
                  >descubre</button>
                </div>
              </SplideSlide>
            </Splide>            
          </div>
        </div>
      </div>
</template>
<script>
import config from "@/config";
import { Splide, SplideSlide } from "@splidejs/vue-splide";
import "@splidejs/splide/dist/css/themes/splide-default.min.css";

const SLIDE_CONFIG = {
        direction   : "ltr",
        rewind      : true,
        wheel       : true,
        pagination  : false,
        autoplay    : false,
        height      : "auto",
        width       : "200rem",
        fixedWidth  : "30rem",
        fixedHeight : "50rem",
        breakpoints : {
          640: {
              height: "auto",
              width: "270px",
              fixedWidth: "30rem",
              fixedHeight: "10rem"
          }
        }
    }
export default {
    name: "PoleSlide",
    props: {
        title:''
    },
    components: {
        Splide,
        SplideSlide,
    },
    data() {
        return {
            slide_options: SLIDE_CONFIG,
            poles: [
                {
                img: config.gallery_url + "general/destinos1.png",
                country: "Cuba",
                name: "Cuba",
                keyword: "cuba"
                },
                {
                img: config.gallery_url + "general/destinos2.png",
                country: "Mexico",
                name: "Cancun",
                keyword: "RQ"
                },
                {
                img: config.gallery_url + "general/destinos3.png",
                country: "Republica Dominicana",
                name: "Punta Cana",
                keyword: "RQ"
                }
            ],
        }
    },
    methods: {
      goSinglePole(keyword, params) {
        if (keyword == "RQ") {
          this.$emit('RQsinglePole',params);
        } else {
          this.$router.push({
            name: "SinglePole",
            params: { keyword }
          });
        }
      },
    }

}
</script>
<style scoped>
.home-poles-list {
  background: linear-gradient(
    360deg,
    rgba(32, 38, 88, 1) 0%,
    rgba(9, 9, 121, 1) 36%,
    rgba(36, 54, 131, 1) 100%
  );
}
.home-poles-list figure figcaption {
  font-size: 3rem;
  font-family: Clarissa;
  top: calc(50% - 100px) !important;
  /* background: rgba(0,0 ,0 ,0.6 ); */
}
@media screen and (max-width: 640px) {
  .home-poles-list figure figcaption {
    font-size: 4rem;
    top: calc(70% - 100px) !important;
  }  
}    
</style>