<template lang="">
    <article class="md:max-w-18rem">
        <figure class="pb-3">
            <img class="rounded-t-2xl" :src="item.img" :alt="item.pole" />
        </figure>
        <!-- <div
        v-match-heights="{el: ['p']}"
        class="border-dashed border-2 rounded-b-2xl border-blue px-5 py-3"
        > -->
        <div        
        class="border-dashed border-2 rounded-b-2xl border-blue px-5 py-3"        
        >
            <h3 class="text-xl xs:text-3xl md:text-4xl text-blue uppercase font-black font-DIN-Medium">{{ item.name }}</h3>
            <span class="font-Clarissa text-blue text-3xl">{{ item.country }}</span>

            <span class="uppercase block font-DIN-Bold text-blue font-black">desde</span>

            <span
                class="price block font-DIN-Bold text-orange text-3xl xs:text-4xl md:text-5xl leading-3 font-bold pt-4"
            >${{ item.price }}</span>
            <p class="offer-description text-blue bg-blueGray py-3 pt-6 px-4" style="height: 150px">{{ item.description }}</p>

            <small>{{ item.info }}</small>
        </div>
        <button
        class="btn-booking font-DIN-Bold w-full text-xl md:text-2xl my-5 
        hover:shadow-md hover:"
        @click="goOffer(item.keyword, {
            country: 'Cuba',
            product: item.name,
            product_name: item.description,
            price: item.price,
        })"
        >{{ item.btn }}</button>
    </article>
</template>
<script>
import { emit } from 'process';

export default {    
    name: 'Offer',
    props: {
        item: {}
    },
    created() {        
    },
    methods: {
        goOffer(keyword, params) {
            this.$emit('offer', {keyword, params})
        }
    }
}
</script>
<style scoped>
article {
    width: 45%;
}  
@media screen and (max-width: 520px) {
  p {
    height: 270px !important;
  }
}
</style>